.override {
  height: 100vh;
  width: 100vw;
  background-color: rgba(107,114,128,.75);
  position: absolute;
  top: 0;
  z-index: 1000;
  user-select: none;
}

.hidden {
  display: none;
}


.card {
  width: 327px;
  background-color: #fff;
  border-radius: 8px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  padding: 20px 20px 16px 20px;
}

.button-box {
  display: flex;
  justify-content: flex-end;
  padding-bottom: 12px;
  cursor: pointer;
}

.card-header {
  display: flex;
  justify-content: space-between;
  padding: 8px 16px;
  background-color: #084f7f;
  color: #fff;
}

.card-header h1 {
  font-size: 16px;
}

.question-mark-icon {
  display: flex;
  justify-content: center;
  align-items: center;
  color: rgb(8, 79, 127);
  border: 1px solid #fff;
  width: 22px;
  height: 22px;
  background-color: #fff;
  border-radius: 50%;
  margin-top: 10px;
  font-size: 14px;
  cursor: pointer;
}


.content-box {
  border: 2px solid rgb(8, 79, 127);
  padding: 16px;
}

.content-box img {
  width: 125.5px;
  height: 106.281px;
  margin: 0 auto;
}

.content-box h2 {
  font-size: 20px;
  color: #084f7f;
  text-align: center;
  margin: 16px 0;
}

.content-box .qrcode {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
}

.content-box canvas {
  margin: -30px 0 -10px 0;
}

.override p {
  color: #084f7f;
} 

.label {
  font-weight: 700;
}

.override .instructions {
  margin-top: 8px;
  color: #c81e1e;
}